nav {
    align-items: center;
    background-color: #192734;
    box-shadow: 0px 2px 7px rgb(136, 0, 255);
    display: flex;
    justify-content: space-between;
    min-height: 100px;
    padding: 0 20px;
}
a {
    text-decoration: none;
    color: rgb(255, 5, 255);
}
nav .nav-brand {
    color: white;
    font-weight: bold;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 1rem;
    cursor: pointer;
}
nav .name{
    color: rgb(136, 0, 255);
    text-align: center;
}

.toolbox {
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.toolbox label {
    color: white;
    font-weight: bold;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    display: block;
    text-align: center;
}

.toolbox>div {
    display: flex;
    justify-content: space-between;
}

.toolbox button {
    background-color: transparent;
    border: 2px solid rgb(136, 0, 255);
    border-radius: 10px;
    color: #f1f1f1;
    cursor: pointer;
    font-size: 1em;
    font-weight: bold;
    margin: 0 20px;
    outline: none;
    padding: 13px 10px;
}
.toolbox button:hover{
    background-color: rgb(136, 0, 255);
}

.toolbox select,
input[type='range'] {
    cursor: pointer;
    margin: 0 20px;
}

.toolbox input,
select,
button {
    min-width: 130px;
}

.toolbox select {
    background: transparent;
    border: 2px solid rgb(136, 0, 255);
    border-radius: 5px;
    /* border: none; */
    color: white;
    font-size: 1em;
    font-weight: bold;
    padding: 5px 5px;
    text-align: center;
}
.toolbox option{
    background: rgb(136, 0, 255);
}

@media (max-width: 992px) {
    nav {
        flex-direction: column;
    }

    .toolbox {
        display: block;
        text-align: center;
    }

    .toolbox>div {
        margin: 10px 0;
    }

    .speed {
        display: none;
    }
}